.eum_day_number_wrapper {
  display: inline-block;
}

.eum_logger_additional_options {
  display: none;
}

#logs-clearing, #delay-updates {
  width: 80px;
}

#anonymize-updates-wrapper label {
  display: block;
  margin-bottom: 5px;
}

#save-anonymize-update-option {
  margin-top: 15px;
}
#log-export {
  position: relative;
}
.export-date-range {
	width: 400px;
	position: absolute;
	z-index: 1001;
  background: #FFF;
}
.export-date-range {
	border: 1px solid #ccc;
	padding: 15px;
	background: #fff;
	box-shadow: 0px 5px 20px rgba(0,0,0,.2);
	text-align: center;
}
.export-date-range .export-button .button-secondary {
  display: inline-block;
  margin: 0;
}
.export-date-range label {
    font-size: 12px;
    color: #333;
    font-weight: bold;
    display: block;
    padding: 0 0 4px 0;
    text-align: left;
    text-shadow: none;
}
.export-date-range .eum-export-date {
    display: inline-block;
    width: 150px;
    background: #fff;
    font-size: 12px;
    color: #333;
    max-width: none;
    padding: 1px 10px;
}
#TB_ajaxWindowTitle {
  text-align: center;
}
#TB_window {
  background: transparent !important;
  box-shadow: none !important;
}
#TB_ajaxContent {
	background: #fff;
}
.export-table {
  margin-bottom: 90px;
}
form#form-export-options {
  margin: 0;
}
#export-options {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  width: 100%;
  height: 30px;
  bottom: 0;
  padding: 20px 10px;
  background-color: #0a8849;
  color: #FFF;

  .button,
  .button-primary,
  .button-secondary {
      display: inline-block;
      text-decoration: none;
      font-size: 13px;
      line-height: 26px;
      height: 28px;
      margin: 0;
      padding: 0 10px 1px;
      cursor: pointer;
      border-width: 1px;
      border-style: solid;
      -webkit-appearance: none;
      border-radius: 3px;
      white-space: nowrap;
      box-sizing: border-box;
  }
}
.export-table thead,
.export-table tfoot {
  tr {
    background: #333;
    th {
      color: #FFF;
    }
  }
}
.export-logs h3 {
  text-align: center;
}